<template>
    <div class="div">
        <input type="text" placeholder="请输入手机号" ref="messge">
        <div class="button">
            <button @click="getCaptures($refs.messge.value)">点击获取验证码</button>
            <div class="wai"></div>
        </div>
        <span @click="closeNumber">X</span>
    </div>
</template>
<script>
import {mapState,mapMutations,mapActions,mapGetters} from 'vuex';
export default {
    data(){
        return{
            number:'11'
        }
    },
    computed:{
        num:state=>state.Quotation.num
    },
    methods:{
        ...mapMutations({
            closeNumber:'Quotation/closeNumber'
        }),
        ...mapActions({
            getCaptures:'Quotation/getCaptures'
        })
    }
}
</script>
<style lang="scss" scoped>
    .div{
        width:5rem;
        height:5rem;
        background: rgba(0,0,0,0.5);
        position: absolute;
        top:40%;
        left:1.3rem;
        display: flex;
        flex-direction: column;
        z-index: 1000;
        input{
            margin-top: 1.4rem;
            width: 85%;
            height: 1rem;
            margin-left: .45rem;
            text-align: center;
            font-size: .22rem;
            border-radius: .3rem;
        }
        .button{
            width: 85%;
            margin-top: .3rem;
            margin-left: .45rem;
            button{
                width: 100%;
                height: 1rem;
                background: skyblue;
                color: white;
                border: none;
                border-radius: .3rem;
           }

        }
        span{
            position: absolute;
            right: 0;
            top:0;
            width:.5rem;
            height: .5rem;
            display: inline-block;
            background: white;
            border-radius: 50%;
            color:black;
            text-align: center;
            line-height: .5rem;
        }
    }
</style>





